import {ApplySalesorder} from 'api/user'
export default{
	data(){
		return{
			data:{}
		}
	},
	methods:{
		open(flag){
			uni.navigateTo({
				url:'/pages/user/distribution/orderdetail?flag='+flag
			})
		},
		initData(){
			ApplySalesorder({
				
			}).then(res=>{
				if(res.result){
					this.data = res
				}	
			})
		}
	},
	onLoad() {
		this.initData()
	}
	
	
}
